@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  @apply bg-white dark:bg-black text-gray-900 dark:text-white;
}
@keyframes spin-slow {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.animate-spin-slow {
  animation: spin-slow 20s linear infinite;
}

